/**
 * General utility classes
*/

/** Screen reader only styles */
%sr-only,
.sr-only:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
}

.wrapper {
  margin-right: auto;
  margin-left: auto;
  padding-right: var(--scale-500);
  padding-left: var(--scale-500);
}

/** Conditional js enabled styles */
html.js-disabled {
  .js-enabled {
    display: none;
    visibility: hidden;
  }
}
html:not(.error.js-disabled) {
  body .js-disabled {
    display: none;
    visibility: hidden;
  }
}
